export function Spinner() {
	return <svg style = { { height: '1em' } } class = 'spinner' viewBox = "0 0 100 100" xmlns = "http://www.w3.org/2000/svg"><circle cx = "50" cy = "50" r = "45"/></svg>
}

export function BigSpinner() {
	return <svg style = { { height: '2em' } } class = 'spinner' viewBox = "0 0 100 100" xmlns = "http://www.w3.org/2000/svg"><circle cx = "50" cy = "50" r = "45"/></svg>
}

